// --- UPDATED SELECT COMPONENT (Restored Inline Version) ---
const Select = ({ label, value, options, onChange }: { label: string, value: string, options: SelectOption[], onChange: (v: string) => void }) => {
  const [isOpen, setIsOpen] = useState(false);
  const selectedOption = options.find((o) => o.value === value) || options[0];

  return (
    <div className="relative group z-20">
      <label className="block text-[11px] font-bold text-white/40 uppercase mb-2 ml-1 tracking-wider">{label}</label>
      <button
        onClick={() => setIsOpen(!isOpen)}
        className={`w-full bg-[#0a0d14]/50 border rounded-xl px-4 py-3 text-left flex items-center justify-between transition-all duration-200 
          ${isOpen ? 'border-[#00C26D]/50 ring-1 ring-[#00C26D]/50 bg-[#131720]' : 'border-white/10 hover:border-white/20'}
        `}
      >
        <div className="flex items-center gap-2.5">
          <span className="text-base">{selectedOption.icon}</span>
          <span className="text-[13px] text-white/90 font-medium">{selectedOption.label}</span>
        </div>
        <ChevronDown className={`w-4 h-4 text-white/30 transition-transform duration-200 ${isOpen ? 'rotate-180 text-[#00C26D]' : ''}`} />
      </button>
      
      <AnimatePresence>
        {isOpen && (
          <>
             {/* Page-wide backdrop to close dropdown */}
             <div className="fixed inset-0 z-[9998]" onClick={() => setIsOpen(false)} />
             
             {/* Premium Floating Dropdown (Inline) */}
             <motion.div
                initial={{ opacity: 0, scale: 0.98, y: 5 }}
                animate={{ opacity: 1, scale: 1, y: 0 }}
                exit={{ opacity: 0, scale: 0.98, y: 5 }}
                transition={{ duration: 0.16, ease: "easeOut" }}
                className="absolute left-0 w-full z-[9999] mt-2 rounded-xl bg-[#131720]/95 border border-white/10 shadow-[0_0_25px_rgba(0,194,109,0.15)] backdrop-blur-md overflow-hidden"
             >
                <div className="max-h-[280px] overflow-y-auto custom-scrollbar p-1">
                  {options.map((option) => (
                    <div
                        key={option.value}
                        onClick={() => {
                          onChange(option.value);
                          setIsOpen(false);
                        }}
                        className={`p-2.5 rounded-lg flex items-start gap-3 cursor-pointer transition-all ${
                          value === option.value 
                            ? "bg-[#00C26D]/10 border border-[#00C26D]/20" 
                            : "border border-transparent hover:bg-white/5"
                        }`}
                    >
                        <span className="text-lg mt-0.5">{option.icon}</span>
                        <div>
                          <div className={`text-[13px] font-medium ${value === option.value ? 'text-[#00C26D]' : 'text-white/90'}`}>
                            {option.label}
                          </div>
                          <div className="text-[11px] text-white/40 leading-tight mt-0.5">{option.description}</div>
                        </div>
                    </div>
                  ))}
                </div>
             </motion.div>
          </>
        )}
      </AnimatePresence>
    </div>
  );
};